export default function () {
    gsap.registerPlugin(ScrollTrigger);

	
    const about = document.querySelector('.about');
	const aboutInfo = document.querySelector('.about__info');
	const aboutTitle = document.querySelector('.about__title')
	const aboutBtn = document.querySelector('.about__btn')
	const aboutImg = document.querySelector('.about__img')
	
	const aboutTimeline = gsap.timeline({
		scrollTrigger: {
			trigger: about,
			start: "top 100%",
		}
	});
	
	
	
	aboutTimeline.from(aboutTitle, { opacity: 0, y: "-100%", duration: 0.5, ease: "power2.inOut" })
	.from(aboutInfo, { opacity: 0, x: "-100%", duration: 0.5, ease: "power2.inOut" })
	.from(aboutBtn, { opacity: 0, y: "100%", duration: 0.5, ease: "power2.inOut" })
	.from(aboutImg, { opacity: 0, duration: 0.5, ease: "power2.inOut" })

}